import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApiResponse } from 'src/app/models/api-response.model';
import { environment as env } from 'src/environments/environment';
import { Order } from '../../products/components/checkout/order-over-view.model';
import { User } from '../model/user';

@Injectable({
  providedIn: 'root'
})
export class ProfileService {

  constructor(private http: HttpClient) { }


  getUserData(userId) {
    return this.http.get<ApiResponse<User>>(`${env.baseUrlWithoutVersion}Account/GetUserById?id=${userId}`);
  }

  updateBasicUser(userId, userData) {
    return this.http.put<ApiResponse>(`${env.baseUrlWithoutVersion}Account/updateBasic-${userId}`, userData);
  }
  updateBusinessUser(userId, userData) {
    return this.http.put<ApiResponse>(`${env.baseUrlWithoutVersion}Account/updateBusiness-${userId}`, userData);
  }

  getAllTrackOrder(id) {
    return this.http.get<ApiResponse>(`${env.baseUrl}Order?FilterValue[userId]=${id}`);
  }
  getAllTrackOrderinfo(id) {
    return this.http.get<ApiResponse>(`${env.baseUrl}OrderInfo?FilterValue[orderNumber]=${id}`);
  }
  getOrderByOrderNumber(orderNumber: number) {
    return this.http.get<ApiResponse<Order>>(`${env.baseUrl}Order/byOrderNumber-${orderNumber}`)
  }
}
