import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ApiResponse } from 'src/app/models/api-response.model';
import { ConfirmEmailRequest, ForgetPasswordRequest, LoginRequest, RegisterationRequest, RegisterationRequestBusiness, ResetPasswordRequest } from 'src/app/modules/auth/models';

@Injectable({
  providedIn: 'root'
})
export class AuthApiService {
  baseUrl :string = 'http://mazenebada-001-site1.itempurl.com/api';
  prefix : string = 'Account'
  constructor(
    private http:HttpClient
  ) {}

  login(requestBody:LoginRequest){
    return this.http.post<any>(`${this.baseUrl}/${this.prefix}/authenticate`,requestBody,{observe:'body'})
  }

  forgetPassword(requestBody:ForgetPasswordRequest){
    return this.http.post<ApiResponse>(`${this.baseUrl}/${this.prefix}/forgot-password`,requestBody)
  }

  resetPassword(requestBody:ResetPasswordRequest){
    return this.http.post<ApiResponse>(`${this.baseUrl}/${this.prefix}/reset-password`,requestBody)
  }

  register(requestBody:RegisterationRequest){
    return this.http.post<ApiResponse>(`${this.baseUrl}/${this.prefix}/register`,requestBody)
  }
  registerBusiness(requestBody:RegisterationRequestBusiness){
    return this.http.post<ApiResponse>(`${this.baseUrl}/${this.prefix}/register-business`,requestBody)
  }
  confirmEmail(requestParams:ConfirmEmailRequest){
    return this.http.get<ApiResponse>(`${this.baseUrl}/${this.prefix}/confirm-email?userId=${requestParams.userId}&code=${requestParams.code}`)
  }
}
